import { useState, useEffect } from "react";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Label } from "./ui/label";
import { Textarea } from "./ui/textarea";
import { RadioGroup, RadioGroupItem } from "./ui/radio-group";
import { Checkbox } from "./ui/checkbox";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "./ui/select";
import { Card } from "./ui/card";
import { Plus, Minus, Send, Users, Utensils, Loader2 } from "lucide-react";
import { toast } from "sonner";
import { Invite } from "../App";

interface Guest {
  id: string;
  firstName: string;
  lastName: string;
  isChild: boolean;
  menuPreference?: string;
}

interface Submission {
  firstName: string;
  lastName: string;
  phone: string;
  attendance: boolean;
  needsAccommodation: boolean;
  accommodationType: string | undefined;
  specialMessage: string;

  guests: Guest[];
}

interface Props {
  api_key: string | null;
  invite: Invite;
  maxGuests?: number;
}

const menus = [
  {
    value: " 1 -  ",
    short: " 1 -  ",
    description: `   ,        ,     ,    
       ,     
    ,       `,
  },
  {
    value: " 2 -  ",
    short: " 2 -  ",
    description: `     ,      
       
               `,
  },
  {
    value: "  - ",
    short: "  - ",
    description: `     , ,    
    ,    
  ,  , ,   `,
  },
  {
    value: "  - ",
    short: "  - ",
    description: `     , ,    
    ,    
      ,      - `,
  },
  {
    value: " ",
    short: " ",
    description: `   , , ,  ,  ,    
   ,    
  ,     `,
  },
];

const accommodationOptions = [
  {
    value: "double",
    label: "105  -       ",
  },
  { value: "triple", label: "115  -  " },
];

export function RSVPForm({ api_key, invite, maxGuests }: Props) {
  const hasElapsed = Date.now() > invite.answerUntilTimestamp;

  const answerUntil = new Date(invite.answerUntilTimestamp);
  const textAnswerUntilDate = answerUntil.toLocaleDateString("bg-BG", {
    day: "numeric",
    month: "long",
    year: "numeric",
  });

  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    phone: "",
    attendance: "",
    needsAccommodation: "",
    specialMessage: "",
  });

  const [guests, setGuests] = useState<Guest[]>([
    {
      id: Date.now().toString(),
      firstName: "",
      lastName: "",
      isChild: false,
      menuPreference: "",
    },
  ]);
  const [loading, setLoading] = useState(false);
  const [submitted, setSubmitted] = useState(false);
  const [accommodationType, setAccommodationType] = useState("double");

  useEffect(() => {
    if (formData.attendance === "yes") {
      setGuests((prev) =>
        prev.map((g, i) =>
          i === 0
            ? {
                ...g,
                firstName: formData.firstName,
                lastName: formData.lastName,
              }
            : g,
        ),
      );
    }
  }, [formData.firstName, formData.lastName, formData.attendance]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (
      !formData.firstName ||
      !formData.lastName ||
      !formData.phone ||
      !formData.attendance
    ) {
      toast.error(",    ");
      return;
    }

    if (formData.attendance === "yes") {
      // Check guests' menuPreference
      for (const guest of guests) {
        if (!guest.menuPreference && !guest.isChild) {
          toast.error(",      ");
          return;
        }
      }

      // Check accommodation
      if (!formData.needsAccommodation) {
        toast.error(",      ");
        return;
      }
    }

    const submission: Submission = {
      firstName: formData.firstName,
      lastName: formData.lastName,
      phone: formData.phone,
      attendance: formData.attendance === "yes",
      needsAccommodation: formData.needsAccommodation === "yes",
      accommodationType:
        formData.needsAccommodation === "yes" ? accommodationType : undefined,
      specialMessage: formData.specialMessage,
      guests: formData.attendance === "yes" ? guests.map((g) => ({
        ...g,
        menuPreference: g.isChild ? undefined : g.menuPreference,
      })) : [{ ...guests[0], firstName: formData.firstName, lastName: formData.lastName, menuPreference: undefined }],
    };
    setLoading(true);
    fetch(
      `https://script.google.com/macros/s/${invite.googleScriptRSVP}/exec?key=${api_key}`,
      {
        redirect: "follow",
        method: "POST",
        body: JSON.stringify(submission),
      },
    )
      .then((response) => {
        toast.success(
          "   !      !",
        );
        setSubmitted(true);
      })
      .catch((err) => {
        console.log("Error submitting RSVP:", err);
        toast.error("   . ,  .");
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const handleFormChange = (field: string, value: string) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
  };

  const addGuest = () => {
    const newGuest: Guest = {
      id: Date.now().toString(),
      firstName: "",
      lastName: "",
      isChild: false,
      menuPreference: "",
    };
    setGuests((prev) => [...prev, newGuest]);
  };

  const removeGuest = (id: string) => {
    if (guests.length > 1) {
      setGuests((prev) => prev.filter((guest) => guest.id !== id));
    }
  };

  const updateGuest = (
    id: string,
    field: keyof Guest,
    value: string | boolean,
  ) => {
    setGuests((prev) =>
      prev.map((guest) =>
        guest.id === id ? { ...guest, [field]: value } : guest,
      ),
    );
  };

  return (
    <section id="rsvp" className="py-20 px-6 bg-parchment">
      <div className="max-w-3xl mx-auto">
        <div className="text-center mb-12">
          <div className="flex items-center justify-center mb-6">
            <div className="w-8 h-8 bg-sage rounded-md flex items-center justify-center">
              <Users size={16} className="text-parchment" />
            </div>
          </div>
          <h2 className="text-3xl md:text-4xl mb-4 text-olivewood">
                
          </h2>
          <p className="text-lg text-bark">
            ,   {textAnswerUntilDate}
          </p>
        </div>

        {hasElapsed ? (
          <Card className="p-8 bg-wedding-white border-sand shadow-xl text-center">
            <h3 className="text-2xl text-sage mb-4">
                 
            </h3>
            <p className="text-bark">
              ,    ,     
               .
            </p>
          </Card>
        ) : submitted ? (
          <Card className="p-8 bg-wedding-white border-sand shadow-xl text-center">
            <h3 className="text-2xl text-sage mb-4">
                 !
            </h3>
            <p className="text-bark">
                   !
            </p>
          </Card>
        ) : (
          <Card className="p-8 bg-wedding-white border-sand shadow-xl rounded-md">
            <form onSubmit={handleSubmit} className="space-y-8">
              {/* Personal Information */}
              <div className="grid md:grid-cols-2 gap-6">
                <div>
                  <Label htmlFor="firstName" className="text-olivewood">
                     *
                  </Label>
                  <Input
                    id="firstName"
                    value={formData.firstName}
                    onChange={(e) =>
                      handleFormChange("firstName", e.target.value)
                    }
                    className="mt-2 border-sand focus:border-sage h-12"
                    required
                    disabled={loading}
                  />
                </div>

                <div>
                  <Label htmlFor="lastName" className="text-olivewood">
                     *
                  </Label>
                  <Input
                    id="lastName"
                    value={formData.lastName}
                    onChange={(e) =>
                      handleFormChange("lastName", e.target.value)
                    }
                    className="mt-2 border-sand focus:border-sage h-12"
                    required
                    disabled={loading}
                  />
                </div>
              </div>

              <div>
                <Label htmlFor="phone" className="text-olivewood">
                   *
                </Label>
                <Input
                  id="phone"
                  type="tel"
                  value={formData.phone}
                  onChange={(e) => handleFormChange("phone", e.target.value)}
                  className="mt-2 border-sand focus:border-sage h-12"
                  pattern="^[+0-9 ]{8,16}$"
                  required
                  disabled={loading}
                />
              </div>

              {/* Attendance */}
              <div>
                <Label className="text-olivewood mb-4 block">
                    ? *
                </Label>
                <RadioGroup
                  value={formData.attendance}
                  onValueChange={(value) =>
                    handleFormChange("attendance", value)
                  }
                  className="flex gap-8"
                  disabled={loading}
                >
                  <div className="flex items-center space-x-3">
                    <RadioGroupItem
                      value="yes"
                      id="yes"
                      className="border-sage text-sage w-5 h-5"
                    />
                    <Label htmlFor="yes" className="text-bark">
                      ,  !
                    </Label>
                  </div>
                  <div className="flex items-center space-x-3">
                    <RadioGroupItem
                      value="no"
                      id="no"
                      className="border-sage text-sage w-5 h-5"
                    />
                    <Label htmlFor="no" className="text-bark">
                      ,     
                    </Label>
                  </div>
                </RadioGroup>
              </div>

              {formData.attendance === "yes" && (
                <>
                  {/* Guests */}
                  <div className="border-t border-sand/50 pt-8">
                    <div className="flex items-center justify-between mb-6">
                      <div className="flex items-center gap-3">
                        <Users size={20} className="text-sage" />
                        <Label className="text-olivewood text-lg">
                            
                        </Label>
                      </div>
                      <div className="flex items-center gap-2">
                        {(!maxGuests || maxGuests > guests.length) && (
                          <Button
                            type="button"
                            onClick={addGuest}
                            size="sm"
                            variant="outline"
                            className="border-sage text-sage hover:bg-sage hover:text-parchment"
                            disabled={loading}
                          >
                            <Plus size={16} />
                          </Button>
                        )}

                        {guests.length > 1 && (
                          <Button
                            type="button"
                            onClick={() =>
                              removeGuest(guests[guests.length - 1].id)
                            }
                            size="sm"
                            variant="outline"
                            className="border-bark text-bark hover:bg-bark hover:text-parchment"
                            disabled={loading}
                          >
                            <Minus size={16} />
                          </Button>
                        )}
                      </div>
                    </div>

                    <div className="space-y-6">
                      {guests.map((guest, index) => (
                        <Card
                          key={guest.id}
                          className="p-6 bg-sand/20 border-sand/50"
                        >
                          <h4 className="text-olivewood mb-4">
                             {index + 1}
                          </h4>

                          <div className="grid md:grid-cols-2 gap-4 mb-4">
                            <div>
                              <Label className="text-olivewood"> *</Label>
                              <Input
                                value={guest.firstName}
                                onChange={(e) =>
                                  updateGuest(
                                    guest.id,
                                    "firstName",
                                    e.target.value,
                                  )
                                }
                                className="mt-1 border-sand focus:border-sage"
                                required={formData.attendance === "yes"}
                                disabled={index === 0 || loading}
                              />
                            </div>
                            <div>
                              <Label className="text-olivewood">
                                 *
                              </Label>
                              <Input
                                value={guest.lastName}
                                onChange={(e) =>
                                  updateGuest(
                                    guest.id,
                                    "lastName",
                                    e.target.value,
                                  )
                                }
                                className="mt-1 border-sand focus:border-sage"
                                required={formData.attendance === "yes"}
                                disabled={index === 0 || loading}
                              />
                            </div>
                          </div>

                          <div className="flex items-center space-x-3 mb-4">
                            <Checkbox
                              id={`child-${guest.id}`}
                              checked={guest.isChild}
                              onCheckedChange={(checked) =>
                                updateGuest(
                                  guest.id,
                                  "isChild",
                                  checked as boolean,
                                )
                              }
                              className="border-sage data-[state=checked]:bg-sage"
                              disabled={loading}
                            />
                            <Label
                              htmlFor={`child-${guest.id}`}
                              className="text-bark"
                            >
                               ( 13 )
                            </Label>
                          </div>

                          {!guest.isChild && (
                            <div>
                              <Label className="text-olivewood mb-2 flex items-center gap-2">
                                <Utensils size={16} />
                                  
                              </Label>
                              <Select
                                value={guest.menuPreference}
                                onValueChange={(value) =>
                                  updateGuest(guest.id, "menuPreference", value)
                                }
                                disabled={loading}
                              >
                                <SelectTrigger className="border-sand focus:border-sage bg-white text-ellipsis">
                                  <SelectValue placeholder="   *" />
                                </SelectTrigger>
                                <SelectContent className="z-50 bg-white border border-sand shadow-md text-ellipsis">
                                  {menus.map((menu) => (
                                    <SelectItem
                                      key={menu.value}
                                      value={menu.value}
                                      className="text-ellipsis"
                                    >
                                      {menu.short}
                                    </SelectItem>
                                  ))}
                                </SelectContent>
                              </Select>

                              {/* Show full menu description nicely underneath */}
                              {guest.menuPreference && (
                                <div className="mt-2 p-3 bg-sand/20 border border-sand rounded-md text-sm text-bark">
                                  {menus
                                    .find(
                                      (m) => m.value === guest.menuPreference,
                                    )
                                    ?.description.split("\n")
                                    .map((line, idx) => (
                                      <div
                                        key={idx}
                                        className={idx === 0 ? "" : "mt-2"}
                                      >
                                        {line}
                                      </div>
                                    ))}
                                </div>
                              )}
                            </div>
                          )}
                        </Card>
                      ))}
                    </div>
                  </div>

                  {/* Accommodation */}
                  <div>
                    <Label className="text-olivewood mb-4 block">
                          ? *
                    </Label>
                    <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
                      <RadioGroup
                        value={formData.needsAccommodation}
                        onValueChange={(value) =>
                          handleFormChange("needsAccommodation", value)
                        }
                        className="flex gap-8"
                        disabled={loading}
                      >
                        <div className="flex items-center space-x-3">
                          <RadioGroupItem
                            value="yes"
                            className="border-sage text-sage"
                          />
                          <Label className="text-bark">, </Label>
                        </div>
                        <div className="flex items-center space-x-3">
                          <RadioGroupItem
                            value="no"
                            className="border-sage text-sage"
                          />
                          <Label className="text-bark">, </Label>
                        </div>
                      </RadioGroup>
                      <p className="text-sm text-gray-600 max-w-xs md:text-right">
                             .
                      </p>
                    </div>
                  </div>

                  {formData.needsAccommodation === "yes" && (
                    <div className="mt-4">
                      <Label className="text-olivewood mb-2 block">
                           *
                      </Label>
                      <Select
                        value={accommodationType}
                        onValueChange={(value) => setAccommodationType(value)}
                        disabled={loading}
                      >
                        <SelectTrigger className="border-sand focus:border-sage bg-white text-ellipsis">
                          <SelectValue placeholder=" " />
                        </SelectTrigger>
                        <SelectContent className="z-50 bg-white border border-sand shadow-md">
                          {accommodationOptions.map((option) => (
                            <SelectItem key={option.value} value={option.value}>
                              {option.label}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                  )}
                </>
              )}

              {/* Special Message */}
              <div>
                <Label htmlFor="specialMessage" className="text-olivewood">
                     
                </Label>
                <Textarea
                  id="specialMessage"
                  value={formData.specialMessage}
                  onChange={(e) =>
                    handleFormChange("specialMessage", e.target.value)
                  }
                  className="mt-2 border-sand focus:border-sage"
                  placeholder=" ,      ?"
                  rows={4}
                  disabled={loading}
                />
              </div>

              <Button
                type="submit"
                className="w-full bg-sage hover:bg-bark text-parchment h-12"
                size="lg"
                disabled={loading}
              >
                {loading ? <></> : <Send className="mr-2" size={20} />}
                {loading ? "..." : " "}
              </Button>
            </form>
          </Card>
        )}
      </div>
    </section>
  );
}
